html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

p, body {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

form {
  width: 300px;
  margin: 0 auto;
  position: relative;
}

