  width: 100%;
  height: 42px;
  border: 3px solid #f9f0da;
  padding-left: 15px;
}

input:focus {
  border-color: #311c24;
}

.search-button {
  height: 42px;
  width: 42px;
  cursor: pointer;
  background: none;
  border: none;
  position: absolute;
  top: 0;
  right: 0;
}

.search-button:before {
  content: "";
  width: 20px;
  height: 20px;
  margin-right: [icon-margin];
  background-image: url("search.5f27059d.svg");
  background-size: cover;
  font-size: 16px;
  display: inline-block;
}

